import romantic from "./romantic.jpg";
import Counter from "./Counter";
import './Detail.css';

function Detail1() {
  return (
    <>
      <div>
        <img src={romantic} alt={romantic} />
        <h1>로맨틱 홀리데이</h1>
        <strong>감독</strong><span> 낸시 마이어스</span><br/>
        <strong>주연</strong><span> 카메론 디아즈, 케이트 윈슬렛, 주드 로, 잭 블랙</span><br/>
        <strong>줄거리</strong><p>L.A에서 잘 나가는 영화예고편 제작회사 사장인 아만다(카메론 디아즈)는 아름다운 외모에 넘쳐나는 돈,<br/>
        화려한 인맥 등 누가 봐도 성공한 여자다. 부족할 것 없는 그녀에게도 골칫거리가 있었는데<br/>
        그건 바로 맘처럼 되지 않는 연애문제. 같은 회사에 근무하던 남자친구는 회사의 어린 직원과 바람이 나고,<br/>
        그녀는 이 모든 상황이 끔찍하기만 하다.<br/>
        영국 전원의 예쁜 오두막집에 살면서 인기 웨딩 칼럼을 연재하는 아이리스(케이트 윈슬렛).<br/>
        그녀는 순수하고 착한 심성을 지닌 아름다운 여인이지만, 그녀의 남자친구는 그녀와 만인이 지켜보는 가운데<br/>
        다른 여자와의 약혼을 발표한다. 마음에 크나큰 상처를 받은 그녀는 자신의 삶에도 변화가 필요하다는 생각을 하게 된다.<br/>
        6천 마일이나 떨어진 곳에 살고 있던 두 여자는 온라인상에서 ‘홈 익스체인지 휴가’를 보낼 수 있는 사이트를 발견하고<br/>
        2주의 크리스마스 휴가 동안 서로의 집을 바꿔 생활하기로 계획한다. 각각 L.A와 영국으로 날아간 아만다와 아이리스.<br/>
        예쁜 오두막집에서 오직 혼자만의 크리스마스를 보내려고 마음먹고 있던 아만다에게 아이리스의 매력적인 오빠 그레엄(쥬드 로)이<br/>
        불쑥 찾아온다. 첫눈에 호감을 느낀 둘은 조심스럽게 데이트를 시작하지만 그레엄은 자꾸만 아만다와 거리를 두려고 한다.<br/>
        반면 L.A로 간 아이리스는 아만다의 친구이자 영화음악 작곡가인 마일스(잭 블랙)를 만난다.<br/>
        푸근한 외모와 따뜻한 유머감각을 지닌 섬세한 감수성의 이 남자와 서로의 감성을 조금씩 이해하며 특별한 감정을 느끼게 되는데.<br/>
        낯선 여행지, 그러나 왠지 익숙한 이 감정! 진정으로 원하는 삶을 발견하게 되는 특별한 크리스마스 휴가~<br/>
        올 겨울, 당신의 가슴을 따뜻하게 적실 최고의 러브스토리가 시작된다.</p>
      </div>
      <Counter />
    </>
  );
}

export default Detail1;
