import React, { useState } from "react";
import './Counter.css';

function Counter() {
  const [num, setNum] = useState(0);

  const onUp = () => {
    setNum(num+1);
  }

  const onDown = () => {
    setNum(num-1);
  }

  return(
  <div>
    <h2>나의 평점은?</h2>
    <h2>{num}</h2>
    <div className="btn">
    <button onClick={onUp} className="up">Up!</button>
    <button onClick={onDown}>Down!</button>
    </div>
  </div>
)
}

export default Counter;