// import { useState } from "react";
import romantic from "./components/romantic.jpg";
import harry from "./components/harry.jpg";
import { Link } from "react-router-dom";
import './Home.css';

function Home() {
  return (
    <div>
      <h1 className="maintitle">크리스마스 추천 영화</h1>
      <div className="movies">
        <div>
            <Link to='/romanticholiday' className="link">
            <img src={romantic} alt={romantic} />
            <h1 className="title">로맨틱 홀리데이</h1>
            </Link>
        </div>
        <div>
            <Link to='/harrypotter' className="link">
            <img src={harry} alt={harry} />
            <h1 className="title">해리포터와 마법사의 돌</h1>
            </Link>
        </div>
      </div>
    </div>
  )
}

export default Home;